


















































/*测试阶段，以360px为准， 1rem = 10px*/
@import url("//at.alicdn.com/t/font_w2sygrotfh5gsyvi.css");
html{
		padding: 0;
		margin: 0;
}
body,
	div,
	h1,
	h2,
	h3,
	h4,
	h5,
	ul,
	ol,
	li,
	dl,
	dt,
	dd,
	textarea,
	form,
	p,
	figure {
		margin: 0;
		padding: 0;
		font-weight: normal;
		line-height: 100%;
		font-weight: 100;
}
body{
		background-color:rgb(236,242,240);
}
i,span,b{
		font-weight: normal;
		font-style: normal;
		line-height: 100%;
}
ul,
	li {
		list-style: none;
}
a {
		text-decoration: none;
		color: #fff;
}
.fl{
		float: left!important;
}
.fr{
		float: right!important;
}
.clearfix{
		clear: both;
}
#app {
		/*position: relative;*/
}
.app{
		/*position: relative;*/
		z-index: 0;
}
.router-fade-enter-active, .router-fade-leave-active {
	  	transition: opacity .2s;
}
.router-fade-enter, .router-fade-leave-active {
	  	opacity: 0;
}



