
.cutline{
	height: 1px ;
	position: absolute;
	width:98%;
	background: radial-gradient(#d3d3d3 -90%, transparent 100%);
	bottom: 0;
}
.topContrl {
	position: relative;
	padding: 1.6rem;
	cursor: pointer;
}
.listWrap {
	max-height: 35rem;
	width: 100%;
	box-sizing: border-box;
	overflow: auto;
	-webkit-overflow-scrolling: touch;
	padding-bottom: 1.2rem;
}
.listWrap li{
	position: relative;
	padding: 1.6rem;
	font-size: 1.6rem;	
	line-height: 1.7rem;
	cursor: pointer;
}
.listWrap li .infoLeft{
	word-break: keep-all;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	padding-right: 4rem;
}
.infoLeft .songsInfo{
	padding-top: 0.1rem;
}
.listWrap .singer{
	font-size: 1.2rem;
	line-height: 1.6rem;
	color: #ccc;
}
.listWrap li .remove{
	position: absolute;
	right: 1.2rem;
	top:1.6rem;
	cursor: pointer;
}
.currentPlay {
	color: #008000;
}



